.style-button {
  font-size: 12px;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
  color: #313133;
  background: #4FD1C5;
  background: -webkit-gradient(linear, left top, right top, from(rgba(129,230,217,1)), to(rgba(79,209,197,1)));
  background: -webkit-linear-gradient(left, rgba(129,230,217,1) 0%, rgba(79,209,197,1) 100%);
  background: -o-linear-gradient(left, rgba(129,230,217,1) 0%, rgba(79,209,197,1) 100%);
  background: linear-gradient(90deg, rgba(129,230,217,1) 0%, rgba(79,209,197,1) 100%);
  border: none;
  border-radius: 1000px;
  box-shadow: 12px 12px 24px rgba(79,209,197,.64);
  transition: all 0.3s ease-in-out 0s;
  cursor: pointer;
  outline: none;
  position: relative;
}

.style-button:not(.dont-up):hover {
  color: #313133;
  transform: translateY(-6px);
}

.style-button:not(.dont-up)::after {
  content: '';
  width: 30px; height: 30px;
  border-radius: 100%;
  border: 6px solid #00FFCB;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: ring 1.5s infinite;
}

.style-button:hover::after,.style-button.not-show::after{   /*.style-button:focus::after { */
  animation: none;
  display: none;
}

@keyframes ring {
  0% {
    width: 30px;
    height: 30px;
    opacity: 1;
  }

  100% {
    width: 100px;
    height: 100px;
    opacity: 0;
  }
}

/*purgecss end ignore */


