*{
  padding:0;
  margin:0;
  box-sizing: border-box;
}

:root{
  --background-black:rgba(0, 0, 0, 0.2);
  --background-body:rgb(33, 36, 40);
  --pink-color:rgb(255, 1, 79);
  --blue-color:rgb(1, 247, 255);
  --border-bottom-color:rgb(0, 0, 0);
  --violet-color:rgb(152, 3, 197)
}

body{
  color: white !important;
  background-color: rgb(33, 36, 40);
}

.blue-color{
  color:var(--blue-color)
}

.pink-color{
  color:var(--pink-color)
}

ul li{
  list-style:none;
  cursor: pointer !important;
  padding:10px 0px;
}

ul{
  all:unset
}

a{
  text-decoration: none;
}

p{
  margin:0
}

.line-dotted{
  display: -webkit-box;
  -webkit-line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
}

.laptop-scrollbar::-webkit-scrollbar {
  width: 10px;
}

.laptop-scrollbar::-webkit-scrollbar-track {
  background-color: rgb(201, 194, 194);
  border-radius: 10px;
}

.laptop-scrollbar::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: grey;
}

/* purgecss end ignore */


